import authAxois from './requiestHandler';
import './style/adminpanel.css';
//import axios from 'axios';
import { Link } from 'react-router-dom';
import { useEffect, useState } from 'react';
import edit from './images/edit.jpg'
//import { DataGrid, GridColDef } from '@mui/x-data-grid';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';




function Adminpanel(){

    const [valuess,setValues]=useState([])
    const values=[];
  //  let valar=[];
    //let sta=true;
 

    //const options = ['Option 1', 'Option 2', 'Option 3'];
    const [selectedOption, setSelectedOption] = useState('');
  
    const handleSelectChange = (event) => {
      setSelectedOption(event.target.value);
    };

       
    useEffect(()=>{
      
      fetchData();
      const info={
        name:sessionStorage.getItem('nameE')
      }
      authAxois.post('/superadmin/show/wallet',info).then((result)=>{
        console.log(result.data.data[0].wallet)
       // setSelectedOption(result.data.data[0])
       sessionStorage.setItem('walletstatus',result.data.data[0].wallet)
       document.querySelector('.walletamount').innerHTML=result.data.data[0].wallet
      }).catch((e)=>{
        console.log(e)
      })
    
    },[])

   
    const fetchData = async () => {
      try {
        const info = {
          name: sessionStorage.getItem('nameE')
        };
        const response = await authAxois.post('/superadmin/list/info', info);
        console.log(response.data);
        setValues(response.data.data);
        setSelectedOption(response.data.data[0].casher_name)
      } catch (error) {
        console.log(`Error fetching data: ${error}`);
      }
    };



    return(<div className='adminpanel'>
            <ToastContainer />
           <main>
    <header className='header'>
      
          <ul className='smooth'>
         
            <Link to="/auth/admin/game"><li style={{backgroundColor:'black'}}>CASHER LISTS
                 <div class="icon">&#10095;</div>
                 <span className="bottom-line"></span>
             </li>
            </Link> 
             
           <Link>  
             <li>
                 <div class="icon">&#10095;</div>
                  <span className="bottom-line"></span> 
             </li>
            </Link>

          <Link to="/admin/game/transferhistory">
             <li>TRANSFER HISTORY
                 <div class="icon">&#10095;</div>
                  <span className="bottom-line"></span>
             </li>
          </Link> 

             <li>.....
                 <div class="icon">&#10095;</div>
                 <span className="bottom-line"></span>
             </li>
             <li>.....
                 <div class="icon">&#10095;</div>
                 <span className="bottom-line"></span>
             </li>
         </ul>
      
    </header>
    
    <div className='sectionn'>
       <p>DASHBOARD <p style={{marginLeft:'200px',marginTop:'-40px',position:'absolute'}}>WALLET AMOUNT:<p className='walletamount' style={{marginLeft:"130px",marginTop:"-40px",position:"absolute"}}></p></p></p>
      
    <div className='changebar'>
   
  
  <button onClick={()=>{
    document.querySelector('.creatempedit').style.display='block'
  }}>TRANSFER WALLETS</button>

  <button onClick={()=>{
     document.querySelector('.changebar').style.display='none'
     document.querySelector('.createmp').style.display='block'

  }}>ADD CASHER</button>


<button onClick={()=>{
     fetchData()
  }}>REFRESH</button>

  
</div>


<div className='createmp'>
  
   <div className='createone'>
      <div className='createtwo'>
        <h1 onClick={()=>{
          document.querySelector('.createmp').style.display='none'
          document.querySelector('.changebar').style.display='block'
        }}>X</h1>
        
         <input className='intfive' placeholder='CASHER NAME' required/>
         <input className='intthree' type='password' placeholder='CASHER PASSWORD' required/> 

        
        
         <input type='hidden' className='inteleven' value={sessionStorage.getItem('phoneA')}/> 
      <div className='btnfooter'> 
      <br/><br/> 
         <button onClick={()=>{
           const dataa={
                sadmin:sessionStorage.getItem('nameE'),
                cname:document.querySelector('.intfive').value,
                passwords:document.querySelector('.intthree').value,
             
                }
                const regex = /^[A-Za-z0-9]+$/;
                
        if(regex.test(dataa.cname)){     
                toast("your request is being send")
           authAxois.post('/superadmin/create/casher',dataa).then((res)=>{
              if(res.data.statuss==='2xx'){
                  //  alert('New User Has Been Created Successfully')
                    toast.success('New User Has Been Created Successfully')
                  //  
                   // document.querySelector('.createmp').style.display='none'
                   
                      window.location.reload();
                   
              }
              else{
                console.log(res)
               // alert('failed')
                toast('SORRY THE USER NAME ALREADY EXIST OR CHEAK YOUR NETWORK')
              }
           }).catch(e=>toast(e))
          }else{
            toast('ONLY PROPER NAME WITH NUMBER IS ALLOWED')
          }
         }}>CREATE</button> 
      </div>
    </div> 
   </div>
</div>




<div className='creatempedit'>
 
   <div className='createone'>
      <div className='createtwo'>
        <h1 onClick={()=>{
          document.querySelector('.creatempedit').style.display='none'
          document.querySelector('.changebar').style.display='block'
        }}>X</h1>
        
        <div className='allcashernames'>
      <select value={selectedOption} onChange={handleSelectChange}>
        {valuess?.map((option, index) => (
          <option key={index} value={option[0]} style={{padding:'5px',margin:'5px',color:'black'}}>
            {option?.casher_name}
          </option>
        ))}
      </select>
    </div> 

         <input className='wallet' placeholder='WALLET' required/>
         
    
      
      {/* <input className='wallet' placeholder='WALLET AMOUNT' required/>*/} 

        
        
         <input type='hidden' className='inteleven' value={sessionStorage.getItem('phoneA')}/> 
      <div className='btnfooter'> 
      <br/><br/> 
         <button onClick={()=>{
           const dataa={
                sname:sessionStorage.getItem('nameE'),
                cname:selectedOption,
                //cname:document.getElementsByTagName('option').innerHTML,
                wallet:parseInt(document.querySelector('.wallet').value)
             
                }
              
           const info={
               superAgentName:sessionStorage.getItem('nameE'),
               casher_name:selectedOption,
               amount:sessionStorage.getItem('walletstatus'),
               wallet:parseInt(document.querySelector('.wallet').value)
           }     
                //console.log(selectedOption)
                console.log(info)
            if(info.wallet>0){    
                try {
                 // console.log(dataa)
                 
                 authAxois.post('/superadmin/transfer/wallet',info).then((result)=>{
                  toast.success(result.data.message)
        //          document.querySelector('.creatempedit').style.display='none'
                  console.log(result)
                  
                 }).catch((e)=>{
                  alert("SERVER IS BUSY NOW,PLEASE TRY AGAIN LATTER"+e)
                  toast(e)
                  console.log(e)
                 })
                 
                } catch (error) {
                  console.log(`Error fetching data: ${error}`);
                }

                authAxois.post('/agent/transfer/history',info).then((result)=>{
                  console.log(result.data)
                  window.location.reload()
                }).catch((e)=>{
                  console.log(e)
                })
            }  else{
              toast("TRANSFER WALLET CAN NOT BE LESS THAN 0")
            }

//// historiess..................

             
          
         }}>TRANSFER</button> 
      </div>
    </div> 
   </div>
</div>



        <div className='fixsection'>                
        </div>
     <div className='gameroundd'></div>
      <div className='tata'>
      <table>

         <tr> 
            <th>CASHER NAME </th>
            <th>CASHER PASSWORD</th>
            <th>WALLET</th>
            
         </tr>
        
            {
               valuess?.map((item,index)=>
                    (<>
                       <tr>
                        <td key={index}><Link to="/auth/admin/game/allplayers" onClick={()=>{
                          sessionStorage.setItem("employee",`${item?.casher_name}`)
                        }}>{item?.casher_name}</Link></td>
                        <td key={index}><Link to="/auth/admin/game/allplayers">{item?.passwords}</Link></td>
                        <td key={index}><Link to="/auth/admin/game/allplayers">{item?.wallet}</Link></td>
                        
                     </tr> 
                   </>))
                      
                }      
        
       
</table>







      </div>
    </div>

    
  </main>



  <div style={{display:'none'}} className='counterone'></div>
    </div>)
}

export default Adminpanel;