import React, { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const CustomDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(2),
    justifyContent: 'center',
  },
}));

export default function CustomizedDialogs({calledNumbers,cards}) {
  // const calledNumbers = {
  //   b: [5, 100, 2, 12, 3,10],
  //   i: [20,22,0,0, 2,22,23],
  //   n: [38,1,'free',0,0],
  //   g: [58, 51, 51,23,0,63],
  //   o: [61, 19,10,0,74],
  // };
  // const cards = [
  //   {
  //     cardname:'card1',
  //     b: [5, 10, 2, 12, 3],
  //     i: [20, 2, 19, 23, 26],
  //     n: [38, 41, "free", 36, 31],
  //     g: [58, 51, 23, 44, 53],
  //     o: [61, 62, 62, 63, 74]
  //   },
  //   {
  //     cardname:'card2',
  //     b: [8, 10, 2, 12, 3],
  //     i: [20, 2, 19, 23, 26],
  //     n: [3, 41, "free", 36, 31],
  //     g: [58, 51, 23, 44, 53],
  //     o: [61, 62, 62, 63, 74]
  //   },
  // ];

  const [selectedCard, setSelectedCard] = useState(null);
  const [inputNumber, setInputNumber] = useState('');
  const [firstDialogOpen, setFirstDialogOpen] = useState(false);
  const [secondDialogOpen, setSecondDialogOpen] = useState(false);
  const [matchedDiagonals, setMatchedDiagonals] = useState({ diagonal1: false, diagonal2: false });
  const [matchedRows, setMatchedRows] = useState([]);
  const [matchedColumns, setMatchedColumns] = useState([]);

  const handleFirstDialogOpen = () => {
    setFirstDialogOpen(true);
  };

  const handleFirstDialogClose = () => {
    setFirstDialogOpen(false);
  };

  const handleSecondDialogClose = () => {
    setSecondDialogOpen(false);
    setSelectedCard(null); // Reset selected card
    setInputNumber(''); // Clear input field
  };

  const handleInputChange = (event) => {
    setInputNumber(event.target.value);
  };

  const handleCardSearch = () => {
    const number = parseInt(inputNumber);
    const selectedCard = cards.find(card => card.cardname === `card${number}`);
    if (selectedCard) {
      setSelectedCard(selectedCard);
      setFirstDialogOpen(false);
      setSecondDialogOpen(true);
    } else {
      // Display an error message or handle invalid input
      toast("THESE CARD NUMBER IS NOT REGISTERD")
      console.error("Card not found.");
    }
  };
  

  useEffect(() => {
    if (selectedCard) {
      // Check if all diagonal numbers are matched for diagonal 1
      const diagonal1Matched =  calledNumbers.b.includes(selectedCard.b[0])
        && calledNumbers.i.includes(selectedCard.i[1])
        && calledNumbers.g.includes(selectedCard.g[3])
        && calledNumbers.o.includes(selectedCard.o[4]);
      // Check if all diagonal numbers are matched for diagonal 2
      const diagonal2Matched = calledNumbers.b.includes(selectedCard.b[4])
        && calledNumbers.i.includes(selectedCard.i[3])
        && calledNumbers.g.includes(selectedCard.g[1])
        && calledNumbers.o.includes(selectedCard.o[0]);
        console.log(diagonal2Matched);

      // Update matched diagonals state
      setMatchedDiagonals({ diagonal1: diagonal1Matched, diagonal2: diagonal2Matched });

      // Check for matched rows
      const matchedRows = [];
      for (let i = 0; i < selectedCard.b.length; i++) {
        let rowMatched = true;
        for (const column of Object.keys(selectedCard)) {
          const calledColumn = calledNumbers[column];
          if (calledColumn && Array.isArray(calledColumn) && selectedCard[column][i] !== 'free' && !calledColumn.includes(selectedCard[column][i])) {
            rowMatched = false;
            break;
          }
        }
        if (rowMatched) {
          matchedRows.push(i);
        }
      }
      setMatchedRows(matchedRows);

      // Check for matched columns
      const matchedColumns = Object.keys(selectedCard).filter(column => {
        const col = selectedCard[column];
        const numbers = calledNumbers[column];
        if (col && Array.isArray(col)) {
          if (col.every(num => numbers.includes(num))) {
            return true; // Column is completely matched
          }
        }
        return false; // Column is not matched
      });
      setMatchedColumns(matchedColumns);
    }
  }, [selectedCard, calledNumbers]);
  return (
    <>
    <ToastContainer />
      <Button variant="contained" color="primary" onClick={handleFirstDialogOpen}>
        Bingo
      </Button>
      <CustomDialog
        onClose={handleFirstDialogClose}
        aria-labelledby="first-dialog-title"
        open={firstDialogOpen}
      >
        <DialogContent dividers sx={{backgroundColor:'#222222'}}>
          <Typography color={'white'}>Enter a number</Typography>
          <TextField
          sx={{backgroundColor:'white'}}
            autoFocus
            margin="dense"
            id="card-number"
            type="number"
            fullWidth
            value={inputNumber}
            onChange={handleInputChange}
          />
        </DialogContent>
        <DialogActions sx={{backgroundColor:'black'}}>
          <Button onClick={handleCardSearch} variant="contained" color="primary">
            search
          </Button>
        </DialogActions>
      </CustomDialog>
      <CustomDialog
        fullWidth
        onClose={handleSecondDialogClose}
        aria-labelledby="second-dialog-title"
        open={secondDialogOpen}
        
      >
        <DialogContent dividers sx={{backgroundColor:'#222222'}}>
          <Grid container  justifyContent="center" alignItems="center">
            {/* Display BINGO letters */}
            {['b', 'i', 'n', 'g', 'o'].map((key, index) => (
              <Grid key={key} item >
                <Typography paddingLeft={10} alignSelf={'center'} variant="h6" align="center" color={'white'}>{key.toUpperCase()}</Typography>
              </Grid>
            ))}
            {/* Display card numbers */}
            {selectedCard && Object.keys(selectedCard).map((key) => (
  <Grid key={key} item xs={2} xl={2}>
    {Array.isArray(selectedCard[key]) ? ( // Check if it's an array
      selectedCard[key].map((number, index) => (
        <Grid
          key={index}
          style={{
            padding: 30,
            textAlign: 'center',
            backgroundColor:
              matchedDiagonals.diagonal1 &&
              ((key === 'b' && index === 0) ||
                (key === 'i' && index === 1) ||
                (key === 'n' && index === 2) ||
                (key === 'g' && index === 3) ||
                (key === 'o' && index === 4))
                ? '#ebe134'
                : matchedDiagonals.diagonal2 &&
                  matchedDiagonals.diagonal2 &&
                  ((key === 'b' && index === 4) ||
                    (key === 'i' && index === 3) ||
                    (key === 'n' && index === 2) ||
                    (key === 'g' && index === 1) ||
                    (key === 'o' && index === 0))
                  ? '#ebe134'
                  : matchedRows.includes(index) || matchedColumns.includes(key)
                  ? '#ebe134'
                  : calledNumbers[key].includes(number)
                  ? 'red'
                  : 'black',
            color:  matchedDiagonals.diagonal1 &&
            ((key === 'b' && index === 0) ||
              (key === 'i' && index === 1) ||
              (key === 'n' && index === 2) ||
              (key === 'g' && index === 3) ||
              (key === 'o' && index === 4))
              ? 'black'
              : matchedDiagonals.diagonal2 &&
                matchedDiagonals.diagonal2 &&
                ((key === 'b' && index === 4) ||
                  (key === 'i' && index === 3) ||
                  (key === 'n' && index === 2) ||
                  (key === 'g' && index === 1) ||
                  (key === 'o' && index === 0))
                ? 'black'
                : matchedRows.includes(index) || matchedColumns.includes(key)
                ? 'black'
                : calledNumbers[key].includes(number)
                ? 'white'
                : 'white',
            marginBottom: 1,
            marginLeft:1
          }}
        >
          {number === 'free' ? 'Free' : number}
        </Grid>
      ))
    ) : (
      // If it's not an array, render an alternative UI
      null
    )}
  </Grid>
))}

          </Grid>
        </DialogContent>
        <DialogActions sx={{backgroundColor:'#222222'}}>
          
          <Button onClick={handleSecondDialogClose} variant="contained" color="primary">
            Close
          </Button>
        </DialogActions>
      </CustomDialog>
    </>
  );
}
