import axios from 'axios';

//const accessToken=''
const authAxois=axios.create({
       //baseURL:'http://localhost:5000' ,
       //baseURL:'http://primebackend.bingobet.net'
       //baseURL:'https://emergency.bingobet.net'
     //  baseURL:'https://final.bingobet.net'
         baseURL:"https://backcheak.bingobet.net"
})


authAxois.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  config.headers.Authorization =  token ? `Bearer ${token}` : '';
  return config;
});


export default authAxois;